<template>
  
    <div class="map">
      <div id="chart" style="width: 29rem; height: 500px;"></div>
    </div>
    
  
</template>

<script>

import echarts from'echarts';
//
import 'echarts/map/js/china';
import jsonp from 'jsonp';

let option = {
          //
          title: {
            // text: '',
            subtext: '',
            x: 'center',
            textStyle: {
              color: '#9c0505'
            }
            
          },
          //
          tooltip: {  
             trigger: 'item',
             formatter: ' {b}<br/>: {c}'
          },
          //
          visualMap: {
            type: 'piecewise',
            show: true,
            pieces: [
                {min: 10000}, //  max max Infinity
                {min: 1000, max: 9999},
                {min: 100, max: 999},
                {min: 10, max: 99},
                {min: 1, max: 9,},
                {value: 0}, //  value  123 

            ],
            inRange: {
              color: ['#fff', '#ffaa85', '#660208'],
              
            }
          },
          //
          series: [
          {
            type: 'map',
            map: 'china',
            // data: [
            //    {name: '', value: 200},
            //    {name: '', value: 500},
            //    {name: '', value: 20000},
            //    {name: '', value: 2000},
            //    {name: '', value: 200000},
            // ],
            label: {
              show: true,
              color: '#333333',
              fontSize: 12
            },
            zoom: 1.3,
            itemStyle: {
              borderColor: '#cdcdcd',
              color: ''
            },
            //
            emphasis: {
              label: {
                color: '#fff',
                fontSize: 12
              },
              itemStyle: {
                areaColor: 'skyblue'
              }

            }

          },
          ],

          
        }

export default {
 


  data() {
    return {
       myChart: ''
    };
  },
  mounted(){
    this.setCharts()
    this.getData()
  },

  methods: {
    // 
    getData(){
       jsonp('https://interface.sina.cn/news/wap/fymap2020_data.d.json? =1580892522427',(err,data) => {
          var lists = data.data.list.map(item => {
            return {name: item.name, value: item.value}
          })
          //
          option.series[0].data = lists;
          //
          this.myChart.setOption(option);
          // 
          // this.myChart.on("click", function (param){
          //   alert(param.dataIndex+':'+option.series[0].data[param.dataIndex].name);
          // });
       })

    },
    //
    async setCharts(){
        // this.$refs mounted
        this.myChart = echarts.init(document.getElementById('chart'));
        // this.myChart.setOption(option);
      }

  },
}
</script>

<style lang="scss" scoped>
 canvas {
   width: 100%;
 }

 #chart div {
  width: 100% !important;
 }
</style>
