<template>
	<div class="userSuggest">
        <!--  -->
        <div class="nav_top d-flex jc-between ai-center bg-white">
            <div> 
                <el-button type="text" @click="$router.go(-1)">
                    <div class="iconfont icon-back ml-1 fs-xxxl text-dark"></div>
                </el-button>
            </div>
            <div class="fs-xxl text-dark pr-3">
                
            </div>
            <div></div>
        </div>

        <div class="px-3 py-3">
            <div class="fs-md py-2"></div>
            <div class="text mb-3">
              
                <el-input
                rows="9"
                class="textarea"
                type="textarea"
                placeholder=""
                v-model="model.suggest"
                maxlength="30"
                show-word-limit
                >
              </el-input>
              
            </div>
            <div class="fs-md py-2"></div>
            <van-uploader v-model="imgList" multiple :after-read="afterRead"/>
            
            
            <el-button type="danger" class="pt-3" @click="postSuggest"></el-button>
        </div>
      

	</div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  data() {
   return {
    url: '',

    imgList: [],
    model: {
      fileList: [],
      suggest: ''
    }
    

  }
},
methods: {
  async afterRead(file) {
      // 
      var formData = new FormData(); // FormData
      formData.append('file', file.file);
      const res = await this.$http.post(`/user/upload.do?`,formData)
      this.model.fileList.push({url: res.data})
      console.log(this.imgList);
    },
  async postSuggest(){
     if (this.textarea === '') {
      Toast('')
      return
     }
     const res = await this.$http.post('user/suggest.do',this.model)
     if (res.data.code != 0) {
      this.textarea = ''
      Toast(res.data.message)
      this.$router.go(-1);
     }else{
      Toast('')
     }
  }

},
created(){

},
mounted() {   //body
  document.querySelector('body').setAttribute('style', 'background:#f5f5f5')
}, 
    beforeDestroy() {   //body
      document.querySelector('body').removeAttribute('style')
    },

}
</script>

<style lang="scss" scoped>
	.el-button {
		width: 100%;
		
	}
.text {
  // box-shadow: 0 0 4px rgba(0, 0, 0,0.2);
}
.textarea {
  border: none;
  
}
.el-textarea__inner {
  border: none;
}
.el-input__inner {
  border: 1px solid white !important;
}
</style>