<template>
 <div class="collection">
 	<!--  -->
 	<van-sticky :offset-top="0">
 	<van-nav-bar
 	title=""
 	left-text=""
 	left-arrow
 	@click-left="onClickLeft"
 	/>
 	</van-sticky>
 	<!--  -->
 	<div class="px-3 py-3">
 		<!--  -->
 		<div class="miaoshu bg-white px-3 py-3 mb-3">
 			<van-tag type="primary" class="ai-center"><div class="iconfont icon-jishiben pr-1"></div><div></div></van-tag>
 			<div class="d-flex ai-center pt-2">
                <div class="" style="font-weight: bold;"></div>
                <div class="fs-md pl-3 text-primary">{{message.status == 0 ? message.createTime+ ' ': ''}}</div>
            </div>	
 			
 			<div class="d-flex ai-center fs-sm pt-2">
 				<div class="text-grey"></div><div></div>
 			</div>
 			<div class="d-flex ai-center fs-sm pt-2 pb-3 border-bottom">
 				<div class="text-grey"></div><div></div>
 			</div>
 			<div class="fs-sm">
 				,,~
 			</div>

 		</div>
        <!--  -->
 		<div class="miaoshu bg-white px-3 mb-3">
 			<div class="d-flex jc-around text-dark-1 fs-sm">
 				<van-divider :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '0 16px',fontSize: '12px' }">
 					
 				</van-divider>
 			</div>
        </div>
        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex ai-center">
        		<div class="">1.(/)</div><div class="text-primary">()</div>
        	</div>
        	<van-radio-group v-model="message.one" :disabled="status === '0'">
        			<van-cell-group>
        				<van-cell title="" clickable @click="one = '1'">
        					<template #right-icon>      
        						<van-radio name="1" />
        					</template>
        				</van-cell>
        				<van-cell title="" clickable @click="one = '2'">
        					<template #right-icon>
        						<van-radio name="2" />
        					</template>
        				</van-cell>
        			</van-cell-group>
        		</van-radio-group>
        </div>

        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex">
        		<div class="">2.()</div><div class="text-pink" style="word-break: keep-all;">()</div>
        	</div>
        	<van-radio-group v-model="message.two" :disabled="status === '0'">
        		<van-radio class="py-2" name="1"></van-radio>
        		<van-radio name="2"></van-radio>
        	</van-radio-group>
        </div>

        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex ai-center">
        		<div class="">3.</div><div class="text-primary">()</div>
        	</div>
        	<van-radio-group v-model="message.three" :disabled="status === '0'">
        		<van-radio class="py-2" name="1"></van-radio>
        		<van-radio name="2"></van-radio>
        	</van-radio-group>
        </div>

        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex ai-center">
        		<div class="">4.</div><div class="text-primary">()</div>
        	</div>
        	<van-radio-group v-model="message.four" :disabled="status === '0'">
        		<van-radio class="py-2" name="1"></van-radio>
        		<van-radio name="2"></van-radio>
        	</van-radio-group>
        </div>
        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex ai-center">
        		<div class="">5./</div><div class="text-primary">()</div>
        	</div>
        	<van-radio-group v-model="message.five" :disabled="status === '0'">
        		<van-radio class="py-2" name="1"></van-radio>
        		<van-radio name="2"></van-radio>
        	</van-radio-group>
        </div>
        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex ai-center">
        		<div class="">6.</div><div class="text-primary">()</div>
        	</div>
        	<van-radio-group v-model="message.six" :disabled="status === '0'">
        		<van-radio class="py-2" name="1"></van-radio>
        		<van-radio name="2"></van-radio>
        	</van-radio-group>
        </div>

        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex ai-center">
        		<div class="">7.</div><div class="text-primary">()</div>
        	</div>
        	<van-radio-group v-model="message.seven" :disabled="status === '0'">
        		<van-radio class="py-2" name="1"></van-radio>
        		<van-radio class="py-2" name="2"></van-radio>
        		<van-radio name="3"></van-radio>
        	</van-radio-group>
        </div>

        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex ai-center">
        		<div class="">8.</div><div class="text-primary">()</div>
        	</div>
        	<van-radio-group v-model="message.eight" :disabled="status === '0'">
        		<van-radio class="py-2" name="1"></van-radio>
        		<van-radio name="2"></van-radio>
        	</van-radio-group>
        </div>
        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex ai-center">
        		<div class="">9.</div><div class="text-primary">()</div>
        	</div>
        	<van-field
        	v-model="message.nine"
        	rows="2"
        	autosize
            :disabled="status === '0'"
        	type="textarea"
        	maxlength="50"
        	placeholder=""
        	show-word-limit
        	/>
        </div>

        <div class="miaoshu bg-white px-3 py-2 mb-3">
        	<div class="d-flex">
        		<div class="">10.</div><div class="text-primary" style="word-break: keep-all;">()</div>
        	</div>
        	<van-radio-group v-model="message.ten" :disabled="status === '0'">
        		<van-radio class="py-2" name="1"></van-radio>
        		<van-radio name="2"></van-radio>
        	</van-radio-group>
        	
        </div>
        <van-button round block type="info" @click="onSubmit" :disabled="status ==='0'">
        	{{status === '0' ? '' : ''}}
        </van-button>
        
 
 	</div>
    
 	
 </div>	
</template>
<script type="text/javascript">
import {Toast} from 'mint-ui'
	export default {
		data(){
			return {
              message : {
                one: '',
                two: '',
                three: '',
                four: '',
                five: '',
                six: '',
                seven: '',
                eight: '',
                nine: '',
                ten: '',
              },
              status: '1',
              user: {}
			}
		},
		methods: {
			onClickLeft(){
				this.$router.go(-1)
			},
            async getData(){
               const res =await this.$http.get('user/findCollectInfo.do')
                if (res.status  == 200) {
                    this.status = res.data.status;
                    if (res.data.status == '0') {
                        this.message = res.data
                    }else {
                        Toast(' ')
                    }
                    
                    
                }
                
               

            },
			async onSubmit(){
                if (this.message.one && this.message.two &&this.message.three && this.message.four && this.message.five &&this.message.six && this.message.seven && this.message.eight && this.message.nine && this.message.ten) {
                    const res = await this.$http.post('user/collectInfo.do',this.message)
                    console.log(res)
                    if (res.data.code != 0) {
                        this.message = res.data
                        this.status = res.data.status;
                        Toast('~')
                    }else {
                        Toast('')
                    }
                    
                }else {
                    Toast('~')
                }

			}
		},
        created(){
            this.user = this.$store.getters.userInfo;
            this.getData();
        },
		mounted() {   //body
			document.querySelector('body').setAttribute('style', 'background: #f7f8fa')
		}, 
        beforeDestroy() {   //body
        	document.querySelector('body').removeAttribute('style')
        },
	}
</script>

<style lang="scss" scoped>
.miaoshu {
   border-radius: 5%;
}
</style>