<template>
    <div class="addressManage">
       <!--  -->
       <div class="nav_top d-flex jc-between ai-center bg-white">
        <div class="pr-3 mr-3"> 
          <el-button type="text" @click="backDefaultAddress">
            <div class="iconfont icon-back ml-2 fs-xxxl text-dark"></div>
          </el-button>
        </div>
        <div class="fs-xxl text-dark pl-3 ml-3">
          
        </div>
        <div>
          <el-button type="text" @click="$router.push('/addAddress')">
            <div class="mr-2 fs-xxl text-dark"></div>
          </el-button>
        </div>
      </div>
       <!--  -->
       <div class="px-2 pt-2 text-grey"></div>
       <div class="addressList">
          <div class="address_item bg-white px-2  py-3 my-2" v-for="(item,i) in dataList" :key="i">
            <!--  -->
            <div class="top_address" style="height: 70px;" @click="use(i)">
              <div class="py-2 fs-xl"><span class="pr-3">{{item.contact}}</span><span>{{item.phone}}</span></div>
              <div>{{item.address}} {{item.detail}}</div>
            </div>
            <!--  -->
            <div class="d-flex jc-between pt-2 ai-center">
              <el-checkbox v-model="item.isDefault== '1'? true : false" @change="setDefault(item.id)"><span v-if="item.isDefault"></span><span v-if="!item.isDefault"></span></el-checkbox>
              <div class="d-flex">
                <div class="text-grey iconfont icon-jishiben pr-3" @click="edit(item.id)"> </div>
                <div class="text-grey" @click="delAddress(item.id)"></div>
              </div>
            </div>
          </div>
       </div>
       <div v-if="dataList.length<1?true:false" class="bg-white text-c" style="height: 250px;">
        <div class="none">
          <div class="iconfont pb-3">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-dizhi"></use>
            </svg>
          </div>
          <div class="text-grey">~</div>
        </div>

      </div>
    </div>
</template>

<script>
import {Toast, MessageBox} from 'mint-ui';
export default {
  name: "addressManage",
  data() {
    return {
      dataList: [],
      oldVal: ''      
    };
  },
  watch: {
      //   
      '$route.path' :function (oldVal,newVal) {
        // body...
        if (this.oldVal == '/addAddress') {
           this.oldVal = '';
        }
         if (this.oldVal == '/order') {
          this.oldVal = oldVal;
        }
        
      }
    },
  methods: {
    async getAddressList(){
      this.$store.commit('showLoading')
      const res = await this.$http.get(`user/findAddressList.do`);

        if (res.status == 200) {
          this.$store.commit('hideLoading')
          this.dataList = res.data
        }else {
          Toast('')
        }
         
    },
    //
    backDefaultAddress(){
      this.$store.commit('deleteAddress')
            if (this.$store.getters.orderAffirm == null){
                this.$router.push('/user')
            }else {
               this.$router.push('/order')
            }

    },
    use(i){
      
      if (this.$store.getters.orderAffirm != null) {
        this.$router.push({
        path: '/order',
        query: {
          addressObj: JSON.stringify(this.dataList[i])
        }
      })
      }
      
    },
    //   
    async setDefault(i){
      const res = await this.$http.get(`user/updateDefaultAddress.do?id=${i}`)
      
        this.getAddressList()
        Toast('');
     
      
      // const dataList = this.dataList;
      // dataList.forEach((item, index) => {
      //   item.isDefault = index == i;
      // });
      // //   
      // dataList.splice(0, 0, ...dataList.splice(i, 1));
      //
      // const res = await this.$http.post('',this.dataList);
      
    },

    delAddress(id) {
      MessageBox.confirm("").then(
        action => {
          this.$http.get(`user/deleteAddress.do?id=${id}`).then(res => {
            if (res.status == 200) {
              this.getAddressList()
              Toast('')
            }else {
              Toast('')
            }
            
          })

        },
        () => {}
        );
    },
    edit(id){
      this.$store.commit('saveCurrentAddressId',id);
      this.$router.push('/addAddress/'+id)
    }
  },
  created() {
    console.log(this.$store.getters.orderAffirm)
    this.getAddressList()
    // let jwt = this.$store.getters.jwt;
    // if (jwt) {
    //   this.pageLoad();
    // } else {
    //   this.$router.push("login");
    // }
  },
     mounted() {   //body
      document.querySelector('body').setAttribute('style', 'background:#f1f3f4')
     }, 
    beforeDestroy() {   //body
      document.querySelector('body').removeAttribute('style')
    }, 
};
</script>

<style lang="scss" scoped>
.nav_top {
  position: sticky;
  top: 0;
  z-index: 99;
}
.top_address {
  border-bottom: 1px solid #dbdde0;
}
.none {
  padding-top: 50px;
}
.icon {
  font-size: 4.5rem;
}
</style>

