       console.log(res)
       this.footList = res.data.rows
     }
  },
  created() {
    this.getFootList()
     //
     this.$nextTick(function () {
                   var th = this
                   // Map
                    MP(th.ak).then(BMap => {
                          //api
                   // ,
                   var point = new BMap.Point(121.160724,31.173277); 
                  //
                   var geolocation = new BMap.Geolocation();
                   geolocation.getCurrentPosition(function(r){console.log(r.point)
                    var mk = new BMap.Marker(r.point);
                    //alert(''+r.point.lng+','+r.point.lat);
                    var point = new BMap.Point(r.point.lng,r.point.lat);//
                    var gc = new BMap.Geocoder();
                    gc.getLocation(point, function(rs){
                       var addComp = rs.addressComponents; console.log(addComp.city);//
                         th.$store.commit('saveCurrentCity',addComp.city)
                     })
                  },{enableHighAccuracy: true})


                    })

                   
            })
        },
  
};
