<template>
 <div class="classify">
  <div class="top">
  	<!--  -->
   <div class="classify-heard bg-white d-flex jc-between ai-center">
   	<!--  -->
   	 <div> 
   	 	<el-button type="text" @click="$router.go(-1)">
   	 		<div class="iconfont icon-back px-3 fs-xxxl text-dark"></div>
   	 	</el-button>
   	 </div>
   	 <!--  -->
   	 <div class="pl-3">
   	 	<div class="fs-xxl py-3 pl-3 ml-3">
   	 		{{title}}
   	 	</div>
   	 </div>
   	 <!--  -->
   	 <div>
   	 	<el-button class="pr-2 text-dark fs-xxl" type="text" @click="onlink"></el-button>
   	 </div>
   </div>
	<!--  -->
	<div class="px-2 bg-white py-3" style="touch-action: none;">
		<mt-navbar v-model="selected">
			<mt-tab-item id="0">
				<a class="nav-link" @click="getOrderList"></a>
			</mt-tab-item>
			<mt-tab-item id="1">
				<a class="nav-link" @click="getOrderByStatus(1)"></a>
			</mt-tab-item>
			<mt-tab-item id="2">
				<a class="nav-link" @click="getOrderByStatus(2)"></a>
			</mt-tab-item>
			<mt-tab-item id="3">
				<a class="nav-link" @click="getOrderByStatus(3)"></a>
			</mt-tab-item>
			<mt-tab-item id="4">
				<a class="nav-link" @click="getOrderByStatus(4)"></a>
			</mt-tab-item>
        </mt-navbar>
	</div>
  </div>
 	
	<!-- -->
	<div class="classify_contain">
		<div v-for="(item,i) in orderList" :key="i"  class="bg-white classify_contain_item px-3 py-3 my-3">
			<router-link tag="div" :to="'/order/orderInfo/'+item.order.id">
			<div class="d-flex jc-between py-2 mb-2">
				<div class=""></div>
				<div>
					<div class="text-pink" v-if="item.type == 1"></div>
					<div class="text-pink" v-if="item.type == 2"></div>
					<div class="text-pink" v-if="item.type == 3"></div>
					<div class="text-pink" v-if="item.type == 4"></div>
				</div>
			</div>
			<div class="d-flex" v-for="(item2,i2) in item.orderItemList" :key="i2">
				<!--  -->
			<div class="img">
				<el-image
				style="width: 100px; height: 100px"
				:src="item2.image"
				fit="cover">
			</el-image>
			</div>
			<!--  -->
			<div class="px-1 py-2 flex-1 px-2">
				<div class="fs-xl text-dark">{{item2.name}}</div>
				<div class="py-2 text-grey">{{item2.num}}</div>
				<div class="d-flex jc-between pt-3 mt-3"><div class="fs-xl text-dark pr-3">{{item2.price/100}}</div> <div class="text-grey">1</div></div>
			</div>
			</div>
			<div class="d-flex jc-between pt-3">
				
				<div class="">{{item.order.totalNum}}()</div>
				<div class="d-flex"><div>:</div><div class="text-pink fs-xxl">{{item.order.totalNum}}</div></div>
			</div>
			
		</router-link>
		<div class="d-flex jc-between pt-3">
			<el-button v-if="selected == '1'" size="mini" type="" @click="cancel(item.order.id,item.order.orderStatus)"></el-button>
			<el-button v-if="selected == '1'" size="mini" type="danger" @click="pay(item.order.id)"></el-button>
			
			<el-button v-if="selected > '2'" size="mini" type="" @click="$router.pusu('/order/logistics/'+item.order.id)"></el-button>
			<el-button v-if="selected == '3'" size="mini" type="danger" @click=""></el-button>
		</div>
		
		</div>
		
	</div>
	<div v-if="orderList.length<1?true:false" class="bg-white text-c" style="height: 250px;">
		<div class="none">
			<div class="iconfont">
				<svg class="icon order" aria-hidden="true">
					<use xlink:href="#icon-shouhuo"></use>
				</svg>
			</div>
			<div class="text-grey">~</div>
		</div>
		
	</div>
	<!--  -->
	<userLike v-if="orderList== undefined || orderList== null || orderList.length == 0" :title="''" />
 </div>
	
</template>

<script>
import { Navbar, TabItem, TabContainer, TabContainerItem ,Toast} from "mint-ui";
import userLike from "../../components/home/userLike";
	export default {
		data(){
			return {
				selected: "0",
				title: "",
              
              swiperOption: {
              	slidesPerView: 5,
              },
              
              active: 0,
              // 
              orderList: [],
			}
		},
		watch: {
			selected(n) {
				switch (n) {
					case "1":
					this.title = "";
					break;
					case "2":
					this.title = "";
					break;
					case "3":
					this.title = "";
					break;
					case "4":
					this.title = "";
					break;
					default:
					this.title = "";
				}
			}
		},
		methods: {
          getIndex(i){
          	this.active = i
          },
          onlink(){
          	this.$router.push('/botUi')
          },
          async getOrderList(){
          	   this.$store.commit('showLoading')
          	   if (this.$route.params.type) {
          	   	const res = await this.$http.get(`order/findOrderByStatus.do?status=${this.selected}`)
          	   	if (res.data.code != 0) {
          	   		this.$store.commit('hideLoading')
          	   		this.orderList = res.data.reverse();
          	   	}else {
          	   		this.$store.commit('hideLoading')
          	   	}
          	   	if (res.data.message === '') {
          	   		this.$store.commit('hideLoading')
          	   	}
          	   	
          	   }else {
          	   	const res = await this.$http.get(`order/findOrderByStatus.do?`)
          	   	if (res.data.code != 0) {
          	   		this.$store.commit('hideLoading')
          	   		this.orderList = res.data.reverse();
          	   	}
          	   	if (res.data.message === '') {
          	   		this.$store.commit('hideLoading')
          	   	}
          	   }
          	   
               
          },
          async getOrderByStatus(status){
          	this.$store.commit('showLoading')
             const res = await this.$http.get(`order/findOrderByStatus.do?status=${status}`)
             if (res.data.code != 0) {
             	this.$store.commit('hideLoading')
             	this.orderList = res.data.reverse();
             }else {
             	this.orderList = [];
             	this.$store.commit('hideLoading')
             	Toast('')
             }
          },
          async cancel(id,status){
          	 const res = await this.$http.get(`order/deleteOrderById.do?id=${id}`)
				if (res.data.code != 0) {
					const res = await this.$http.get(`order/findOrderByStatus.do?status=${status}`)
					this.orderList = res.data.reverse();
					Toast('')

				}else {
					Toast('')
				}
          },
          pay(id){
          	this.$router.push('/orderPay/'+id)
          }
		},
		created() {
			
			let type = this.$route.params.type;
			if (type) {
				this.selected = type;
			}
			this.getOrderList();
		},
		
           
		
		mounted() {   //body
			console.log(this.orderList)
			document.querySelector('body').setAttribute('style', 'background:#f1f3f4')
		}, 
        beforeDestroy() {   //body
        	document.querySelector('body').removeAttribute('style')
        },
        components: {
        	userLike,

        },
	}
</script>

<style scoped lang="scss">
.none {
   padding-top: 50px;
}
.classify_contain_item {
	border-bottom: 1px solid #eeeeee;
	color: #999;
}
.classify_contain {
	
}
.classify {
	padding-bottom: 60px;
}
.top {
	position: sticky;
	top: 0;
	z-index: 999;
}
.active {
	color: #e32639;

}
a {
	font-size: 14px;

}
.order {
	font-size: 8rem;
}
</style>