import axios from "axios"
import Vue from 'vue'
// 
import router from './router/index.js'
const http = axios.create({
  //   
  
  baseURL: 'http://test.xiaoyang666.top/'  // api  vue.config.js ,
  // baseURL: '/bot/'  // api  vue.config.js ,

})
//
Vue.axios.defaults.withCredentials = true;
//    tonken   
http.interceptors.request.use(function (config) {
  // localStorage token
  if (localStorage.token) {
     // 
    config.headers.Authorization = localStorage.token
  }
   
    return config;
  }, function (error) {
    // Do something with request error
    return Promise.reject(error);
  });


//  token
http.interceptors.response.use(function (response) { // 10010 token30 10011 token
if (response.data.code === 4) {
    router.replace({
        path: '/login' // token
    })
} else if (response.data.token) { // tokentoken
    localStorage.token = response.data.token;
}
    return response
}, function (error) {
    return Promise.reject(error)
})
// http  mian.js
export default http